import React, { useState, useEffect } from "react";
import { Box, Button, Typography, Paper } from "@mui/material";
import { firestore, auth } from "./firebase";
import { doc, getDoc, updateDoc, arrayUnion, setDoc } from "firebase/firestore";
import { onAuthStateChanged } from "firebase/auth";

export default function Shop({ open, onClose }) {
  const [coins, setCoins] = useState(null);
  const [user, setUser] = useState(null);
  const [showTrophies, setShowTrophies] = useState(false);
  const [trophies, setTrophies] = useState([]);

  const items = [
    { id: "motivator", name: "Motivator Badge", cost: 3, emoji: "" },
    { id: "zenMaster", name: "Zen Master", cost: 4, emoji: "" },
    { id: "goldDuck", name: "Golden Duck Trophy", cost: 5, emoji: "" },
    { id: "taskWizard", name: "Task Wizard", cost: 6, emoji: "" },
  ];

  const fetchUserData = async () => {
    if (!user) return;
    const rawUserId = user.uid;
    const coinsDocRef = doc(firestore, "coins", rawUserId);
    const trophiesDocRef = doc(firestore, "trophies", rawUserId);
  
    try {
      const coinsSnap = await getDoc(coinsDocRef);
      setCoins(coinsSnap.exists() ? coinsSnap.data().coins : 0);
  
      const trophiesSnap = await getDoc(trophiesDocRef);
      setTrophies(trophiesSnap.exists() ? trophiesSnap.data().items || [] : []);
    } catch (err) {
      console.error("Error fetching user data:", err);
    }
  };

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
    });
    return unsubscribe;
  }, []);

  useEffect(() => {
    if (open && user) {
      // Fetch coins
      fetchUserData();
    }
  }, [open, user]);

  const spendCoins = async (amount, itemId = null) => {
    if (!user) return;
    const rawUserId = user.uid;
    const coinsDocRef = doc(firestore, "coins", rawUserId);
    const trophiesDocRef = doc(firestore, "trophies", rawUserId);
  
    try {
      const docSnap = await getDoc(coinsDocRef);
      const currentCoins = docSnap.exists() ? docSnap.data().coins || 0 : 0;
  
      if (currentCoins >= amount) {
        await updateDoc(coinsDocRef, { coins: currentCoins - amount });
  
        const trophiesSnap = await getDoc(trophiesDocRef);
        if (!trophiesSnap.exists()) {
          // Create doc with initial item array
          await setDoc(trophiesDocRef, { items: [itemId] });
        } else {
          // Update existing doc
          await updateDoc(trophiesDocRef, { items: arrayUnion(itemId) });
        }
  
        setCoins(currentCoins - amount);
        setTrophies((prev) => [...prev, itemId]);
        alert(`You bought ${itemId} for ${amount} coins! `);
      } else {
        alert("Not enough coins!");
      }
    } catch (error) {
      console.error("Error spending coins:", error);
    }
  };

  const getItemDetails = (id) => items.find((item) => item.id === id);

  if (!open) return null;

  return (
    <Box sx={{ 
      width: "90%", 
      maxWidth: "1400px", 
      margin: "20px auto", 
      padding: 4, 
      background: "linear-gradient(135deg, #e0f7fa 0%, #e0f2f1 100%)", 
      borderRadius: "12px", 
      boxShadow: "0px 6px 15px rgba(0, 0, 0, 0.1)" ,
      justifyContent: "center",
      }}
    >
        {!showTrophies ? (
          <>
            <Typography variant="h4" sx={{ marginBottom: 2, textAlign: "center" }}>
              Spend your Coins! 
            </Typography>
            <Typography variant="h5" align="center">
              {coins > 0 ? (
                <>
                  You have <strong>{coins}</strong> {coins === 1 ? "coin" : "coins"} 
                </>
              ) : (
                <>
                  No coins for you 
                  <br />
                  Complete a quiz to earn some! 
                </>
              )}
            </Typography>
            <Box sx={{ marginTop: 3 }}>
              {items.map((item) => (
                <Paper
                  key={item.id}
                  elevation={3}
                  sx={{
                    marginBottom: 2,
                    padding: 2,
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                    width: "auto",
                    borderRadius: 2, // Rounded corners (8px)
                    boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.08)", // Soft shadow
                    background: "#ffffff", // Optional: ensures consistent contrast
                  }}
                >
                  <Typography variant="body1">
                    {item.emoji} {item.name}  {item.cost} coins
                  </Typography>
                  <Button
                    onClick={() => spendCoins(item.cost, item.id)}
                    disabled={coins === null || coins < item.cost}
                    sx={{
                      backgroundColor: "#f57c00",
                      color: "white",
                      "&:hover": { backgroundColor: "#e65100" },
                    }}
                  >
                    Buy
                  </Button>
                </Paper>
              ))}
            </Box>
          </>
        ) : (
          <>
            <Typography variant="h4" sx={{ marginBottom: 2 }}>
              Trophy Cabinet 
            </Typography>
            {trophies.length > 0 ? (
              <Box sx={{ marginTop: 2 }}>
                {trophies.map((id) => {
                  const item = getItemDetails(id);
                  return (
                    <Paper
                      key={id}
                      elevation={2}
                      sx={{
                        marginBottom: 2,
                        padding: 2,
                        width: "90%",
                        textAlign: "left",
                      }}
                    >
                      <Typography variant="h6">
                        {item?.emoji || ""} {item?.name || id}
                      </Typography>
                    </Paper>
                  );
                })}
              </Box>
            ) : (
              <Typography variant="body1">
                No trophies yet. Earn some by completing tasks! 
              </Typography>
            )}
          </>
        )}

      <Box sx={{ display: "flex", flexDirection: "column", gap: 1.5, width: "100%", alignItems: "center", marginTop: 2 }}>
      <Button
        onClick={() => setShowTrophies((prev) => !prev)}
        sx={{
          backgroundColor: "#00796b",
          color: "#ffffff",
          fontWeight: 600,
          px: 4,
          py: 1.5,
          borderRadius: 2,
          textTransform: "none",
          width: "70%",
          "&:hover": {
            backgroundColor: "#00695c",
          },
        }}
      >
        {showTrophies ? "Back to Shop " : "Trophy Cabinet "}
      </Button>

      <Button
        onClick={fetchUserData}
        sx={{
          backgroundColor: "#00796b",
          color: "#ffffff",
          fontWeight: 600,
          px: 4,
          py: 1.5,
          borderRadius: 2,
          textTransform: "none",
          width: "70%",
          "&:hover": {
            backgroundColor: "#00695c",
          },
        }}
      >
         Refresh Coins
      </Button>

        <Button
          onClick={onClose}
          sx={{
            backgroundColor: "#00796b",
            color: "#ffffff",
            fontWeight: 600,
            px: 4,
            py: 1.5,
            borderRadius: 2,
            textTransform: "none",
            "&:hover": {
              backgroundColor: "#00695c",
            },
          }}
        >
          Back to tasks 
        </Button>
      </Box>
    </Box>
  );
}