import React, { useState } from "react";
import { Box, TextField, Button, Paper } from "@mui/material";

export default function AddText({ addItem }) {
  const [input, setInput] = useState("");

  const handleAdd = () => {
    if (input.trim()) {
      addItem(input.trim());
      setInput("");
    }
  };

  // Handle Enter key press
  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleAdd();
    }
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        // center 
        alignItems: "center",
        justifyContent: "center",
        width: "100%",
        margin: "0 auto",
      }}
    >
      <Paper
        elevation={6}
        sx={{
          padding: "20px",
          borderRadius: "12px",
          width: "55%", // or "100%" if you want full width
          background: "linear-gradient(135deg, #ffffff, #f5f5f5)",
          boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
          textAlign: "center",
        }}
      >
        <TextField
          id="task-input"
          label="Enter Task"
          variant="outlined"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={handleKeyDown}
          fullWidth
          sx={{
            backgroundColor: "#ffffff",
            borderRadius: "8px",
            "& .MuiOutlinedInput-root": {
              borderRadius: "8px",
            },
          }}
        />

        <Button
          variant="contained"
          onClick={handleAdd}
          sx={{
            backgroundColor: "#00796b",
            color: "#ffffff",
            fontWeight: 600,
            mt: 2,
            px: 4,
            py: 1.5,
            borderRadius: "8px",
            textTransform: "none",
            "&:hover": {
              backgroundColor: "#00695c",
            },
          }}
        >
          Add Task 
        </Button>
      </Paper>
    </Box>
  );
}